
.container{
border: 1px solid black;
border-radius: 10px;
width: 290px;
/* display: flex; */
/* justify-content: center; */
margin: 20px;
box-shadow: 2px 2px 20px black;
padding:0 0 10px 0;
text-align: center;
background-color: rgb(184, 116, 226);

}
.container:hover{  
box-shadow: 10px 10px 20px black;
}
.container h1{
  font-size: 20px;
  color: white;
  letter-spacing: 2px; 
  margin: 10px; 
}

.container img{
  display: flex;
  border-radius: 10px;
  width: 100%;
  height: 180px;
}
/* .container img:hover{
width: 130%;
} */
@media(max-width: 800px) {  
  .container{
    width: 100px;
    margin: 22px;
    height: 100px;
    
  }
  .container h1{
    font-size: 5px;
  }
  .container img{
    width: 100%;
    height: 60px;
  }
}
